<template>
    <div class="v-form">
        <!--v-if="options && options[index] && options[index]['type'] && options[index]['type'] != 'hidden'"-->
        <template
            v-for="(val, key, index) in currentValue"
        >
            <!---->
            <template v-if="dividers && dividers[index]">
                <div class="v-form__divider" :key="'dividers_' + index">
                    <slot :name="'slotB' + index">
                        <p v-if="dividers[index]['label']">{{dividers[index]['label']}}</p>
                    </slot>
                </div>
            </template>

            <!---->
            <v-form-item
                v-if="currentOptions && currentOptions[key] && currentOptions[key]['type'] && currentOptions[key]['type'] != 'hidden'"
                :key="'VFI_' + key"
                v-model="currentValue[key]"
                :option="currentOptions[key]"
                :idx="index"
                :attr="key"
                :class="{'last': dividers && dividers[key]}"
                :validator="validators && validators[key]"
            ></v-form-item>

            <!---->
            <template v-if="dividers && dividers[key]">
                <div class="v-form__divider" :key="'dividers_b_' + index">
                    <slot :name="'slotE' + index">
                        <p v-if="dividers[key]['label']">{{dividers[key]['label']}}</p>
                    </slot>
                </div>
            </template>
        </template>
    </div>
</template>
<script>
/**
 * v-form
 * @param value Object 
 * @param options Object disabled
 * @param dividers Object slot
 * @param validators Object 
 *              -- Author by Dio Zhu. on 2018.5.17
 */
import vFormItem from '../vendor/v-form-item';
import vCell from '../vendor/v-cell';
import vRadio from '../vendor/v-radio';
import clickoutside from '../js/utils/clickoutside';
import vPopup from '../vendor/v-popup';
import * as utils from '../js/utils/utils'; //eslint-disable-line
import vPicker from '../vendor/v-picker';
import vDatetimePicker from '../vendor/v-datetime-picker';
import Vue from 'vue';
import Validator from '../vendor/v-validator.js';
Vue.use(Validator);

export default {
    name: 'v-form',

    components: { vFormItem, vCell, vRadio, vPopup, vPicker, vDatetimePicker },
    directives: { clickoutside },
    props: {
        value: {                // 
            type: Object,
            default: () => {}
        },
        options: {              // 
            type: Object,
            default: () => {}
        },
        dividers: {             // 
            type: Object,
            default: () => {}
        },
        validators: {           // 
            type: Object,
            default: () => {}
        }
    },

    data () {
        return {
            currentValue: this.value,
            currentOptions: this.options
        };
    },

    watch: {
        value (val) {
            this.currentValue = val;
        },
        currentValue (val) {
            this.$logger.log('v-formwatch.currentValue: ', val);
            this.$emit('input', val);
        },
        options (val) {
            this.currentOptions = val;
            this.init();
        },
        currentOptions (val) {
            this.$emit('handleChangeOptions', val);
        }
    },

    created () {
        this.init();
    },

    methods: {
        init () {
            this.$logger.log('v-forminit... ');
            for (let key in this.options) { // 
                if (this.options.hasOwnProperty(key) && this.options[key]['displayExpr']) {
                    this.bindWatcher(key, this.options[key]['displayExpr']);
                }
            }
            this.checkValidator(); // 
        },
        bindWatcher (attr, expr) { // watcher~ Author by Dio Zhu. on 2018.5.15
            if (!expr || !expr.exp || !expr.key || !expr.val) return;
            let key = 'currentValue.' + expr.key;
            let fn = (k, v, o) => {
                this.$logger.log('v-formbindWatcher.fn: ', k, v, o);
                if (expr.exp === '!==') {
                    if (expr.val !== v) this.$set(this.options[attr], 'hidden', false);
                    else this.$set(this.options[attr], 'hidden', true);
                }
            };
            this.$watch(key, fn.bind(this, expr.key));
            let arr = expr.key.split('.'), obj = null;
            [].forEach.call(arr, (v, i) => {
                if (i === 0) obj = this.currentValue[v];
                else obj = obj[v];
            });
            fn(obj);
            this.$logger.log('v-formbindWatcher: ', expr, expr.key, obj);
        },
        checkValidator () { // ~ Author by Dio Zhu. on 2018.5.15
            this.$logger.log('v-formcheckValidator: ');
            if (!this.validators) return;
            let fn = (k, v, o) => { // 
                let attr = k.replace('IdentyType', 'IdentyNo'), // 
                    key = this.options[k] && this.options[k]['key'] || 'key', // optionskey
                    val = v[key],   // key
                    vid = this.validators && this.validators[attr] && this.validators[attr]['key']; // validatorkey
                this.$logger.log('v-formcheckValidator.fn: ', k, attr, val, vid);
                if (parseInt(val) === 0 || parseInt(val) === 1) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' }, card: { message: '' } });
                } else if (parseInt(val) === 2) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' }, passport: { message: '' } });
                } else if (parseInt(val) === 3) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' }, sorderIdenty: { message: '' } });
                } else if (parseInt(val) === 4) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' }, socialSecurityCard: { message: '' } });
                } else if (parseInt(val) === 5) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' }, hongKongMacauPasser: { message: '' } });
                } else if (parseInt(val) === 6) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' }, taiwanPasser: { message: '' } });
                } else if (parseInt(val) === 7) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' }, taiwanPasser: { message: '' } });
                } else if (parseInt(val) === 8) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' }, taiwanPasser: { message: '' } });
                } else if (parseInt(val) === 9) { // 
                    this.$set(this.validators, attr, { key: vid, required: { rule: true, message: '' } });
                }
            };
            for (let k in this.validators) {
                if (k.indexOf('IdentyType') >= 0) {
                    this.$watch('currentValue.' + k, fn.bind(this, k));
                }
            }
        }
    }
};
</script>
<style rel="stylesheet/scss" lang="scss">
    @import "../scss/variables";
    @import "../scss/mixins";

    .v-form {

        .v-cell {
            /*min-height: pxTorem(55);*/

            .v-cell__value span {
                color: #1B1B20;
                font-weight: 500;
            }
        }

        .v-form-item.last .v-cell__wrapper{
            border: none;
        }

        .v-form__divider {
            min-height: pxTorem(12);
            background: #F8F9F8;
            overflow: auto;
            border-top: 1px solid #E3E3E3;
            border-bottom: 1px solid #E3E3E3;

            > p {
                box-sizing: border-box;
                height: pxTorem(20);
                margin: pxTorem(11) 0 pxTorem(4) 0;
                padding: 0 0 0 pxTorem(20);
                font-size: pxTorem(14);
                line-height: pxTorem(20);
                color: #9D9D9D;
            }
        }
    }
</style>
