<template>
    <div class="page page-form">
        <h2></h2>

        <div class="attributes">
            <h2>API</h2>
            <table>
                <tr><th></th><th></th><th></th><th></th><th></th></tr>
                <tr><td>value</td><td></td><td>Object</td><td>-</td><td>-</td></tr>
                <tr><td>options</td><td>disabled</td><td>Object</td><td>-</td><td>-</td></tr>
                <tr><td>dividers</td><td>slot</td><td>Object</td><td>-</td><td>-</td></tr>
                <tr><td>validators</td><td></td><td>Object</td><td>-</td><td>-</td></tr>
            </table>
        </div>
        <br/>
        <p class="desc"><b>*</b>validatorsrequired*</p>
        <p class="desc"><b></b>optionsdisabled</p>
        <p class="desc"><b></b>optionstype=pickerpickerType=regionpickerpicker</p>
        <p class="desc"><b></b>optionsdisplayExpr</p>
        <p class="desc"><b></b>validators'IdentyType''IdentyNo'watch</p>

        <h2></h2>
        <v-form v-model="detail" :options="options" :dividers="dividers" :validators="validators"></v-form>
        <!--<div class="blank"></div>-->
        <div class="form-buttons">
            <v-button size="small" @click="handleSubmit"></v-button>
        </div>

        <div class="blank"></div>
    </div>
</template>

<script>
import vForm from '../vendor/v-form';
import vRow from '../vendor/v-row.vue';
import vCell from '../vendor/v-cell.vue';
import vButton from '../vendor/v-button.vue';

export default {
    components: { vRow, vCell, vForm, vButton },
    data () {
        return {
            detail: {},
            options: {},
            dividers: {},
            validators: {}
        };
    },
    mounted () {
        this.$logger.log('form.mounted... ');
        this.init();
    },
    watch: {
        // detail (val) {
        //     this.$logger.log('form.watch.detail: ', val);
        // }
    },
    methods: {
        init () {
            this.$logger.log('form.init... ');
            // this.validator = this.$validation;
            this.$set(this, 'detail', { // pickerAuthor by Dio Zhu. on 2018.5.11
                username: '',
                sex: 1,
                studentIdentyType: {key: 2, values: ''},
                studentIdentyNo: '',
                birthday: '2001-02-03',
                region: [{key: 2, values: ''}, {key: 5, values: ''}, {key: 1754, values: ''}],
                address: '',
                begin_time: '2018-05-20',
                // 
                mainName: '',
                mainIdentyType: {key: 1, values: ''},
                mainIdentyNo: '',
                mainPhone: '',
                // 
                standbyName: '',
                standbyIdentyType: {key: 1, values: ''},
                standbyIdentyNo: '110222199009090099',
                standbyPhone: '18600000002',
                // 
                desc: ''
            });
            // Author by Dio Zhu. on 2018.5.11
            this.$set(this.options, 'username', { label: '', type: 'text', placeholder: '' });
            this.$set(this.options, 'sex', { label: '', type: 'radio', options: [{label: '', value: 0}, {label: '', value: 1}], mode: 'list horizontal', radioClasses: 'disk' });
            this.$set(this.options, 'studentIdentyType', { label: '', type: 'picker', key: 'key', valueKey: 'values', slots: [ { flex: 1, values: [ {key: 1, values: ''}, {key: 2, values: ''}, {key: 3, values: ''}, {key: 4, values: ''}, {key: 5, values: ''}, {key: 6, values: ''}, {key: 7, values: ''}, {key: 8, values: ''}, {key: 9, values: ''} ] } ] });
            this.$set(this.options, 'studentIdentyNo', { label: '', type: 'text', placeholder: '' });
            this.$set(this.options, 'birthday', { label: '', type: 'datetime-picker', pickerType: 'date', placeholder: '', displayExpr: { key: 'studentIdentyType.key', exp: '!==', val: 1 } });
            this.$set(this.options, 'region', { label: '', type: 'picker', pickerType: 'region', placeholder: '', key: 'key', valueKey: 'values', slots: [ { flex: 1, className: 'slot1', textAlign: 'center' }, { flex: 1, className: 'slot2', textAlign: 'center' }, { flex: 1, className: 'slot3', textAlign: 'center' } ] });
            this.$set(this.options, 'address', { label: '', type: 'text', placeholder: '', placeholderRemark: '' });
            this.$set(this.options, 'begin_time', { label: '', type: 'datetime-picker', pickerType: 'date', placeholder: '' });
            // 
            this.$set(this.options, 'mainName', { label: '', type: 'text', placeholder: '' });
            this.$set(this.options, 'mainIdentyType', { label: '', type: 'picker', key: 'key', valueKey: 'values', slots: [ { flex: 1, values: [ {key: 1, values: ''}, {key: 2, values: ''}, {key: 3, values: ''}, {key: 4, values: ''}, {key: 5, values: ''}, {key: 6, values: ''}, {key: 7, values: ''}, {key: 8, values: ''}, {key: 9, values: ''} ] } ] });
            this.$set(this.options, 'mainIdentyNo', { label: '', type: 'text', placeholder: '' });
            this.$set(this.options, 'mainPhone', { label: '', type: 'tel', placeholder: '' });
            // 
            this.$set(this.options, 'standbyName', { disabled: true, label: '', type: 'text', placeholder: '' });
            this.$set(this.options, 'standbyIdentyType', { disabled: true, label: '', type: 'picker', key: 'key', valueKey: 'values', slots: [ { flex: 1, values: [ {key: 1, values: ''}, {key: 2, values: ''}, {key: 3, values: ''}, {key: 4, values: ''}, {key: 5, values: ''}, {key: 6, values: ''}, {key: 7, values: ''}, {key: 8, values: ''}, {key: 9, values: ''} ] } ] });
            this.$set(this.options, 'standbyIdentyNo', { disabled: true, label: '', type: 'text', placeholder: '' });
            this.$set(this.options, 'standbyPhone', { disabled: true, label: '', type: 'tel', placeholder: '' });
            // 
            this.$set(this.options, 'desc', { label: '', type: 'textarea', placeholder: '', limit: 10, alarm: 5 });

            // 
            this.$set(this, 'dividers', {
                0: {label: ''}, // 
                begin_time: {},       // 
                mainPhone: {},       // 
                standbyPhone: {}       // 
            });

            // keyvalidatorAuthor by Dio Zhu. on 2018.5.16
            // this.$set(this.validators, 'username', { key: Object.keys(this.validators).length + '_username', required: { rule: true, message: '~~' }, minLength: { rule: 2, message: '2~' } });
            // 'IdengyType' Author by Dio Zhu. on 2018.5.16
            this.$set(this.validators, 'studentIdentyType', { key: Object.keys(this.validators).length + '_studentIdentyType', required: { rule: true, message: '' } });
            // 'IdengyNo' Author by Dio Zhu. on 2018.5.16
            this.$set(this.validators, 'studentIdentyNo', { key: Object.keys(this.validators).length + '_studentIdentyNo', required: { rule: true, message: '' } });

            // // picker
            // this.$set(this.options.studentIdengyType.slots[0], 'defaultIndex', 1);
        },
        validate () {
            for (let key in this.$validation) {
                if (this.$validation.hasOwnProperty(key)) {
                    for (let k in this.$validation[key]) {
                        if (this.$validation[key].hasOwnProperty(k)) {
                            this.$toast(this.$validation[key][k]);
                            return false;
                        }
                    }
                }
            }
            return true;
        },
        handleSubmit () {
            this.$logger.log('form.handleSubmit... ', this.$validation);
            if (this.validate()) {
                this.$toast('~');
            }
        }
    }
};
</script>

