// === init ===

let Validator = {
        bind (el, binding, vnode) {
            // console.warn('bindvalidation', Vue.$validation, vnode.context);
            if (typeof binding.value === 'undefined') return;
            el[ctx] = {
                el,
                vm: vnode.context,
                expression: binding.value,
                validate_id: binding.value.key || vnode.context._uid
            };
            const args = arguments;
            el[ctx].vm.$nextTick(() => {
                setTimeout(() => { validate.call(el[ctx], args); }, 0);
            });
        },
        // inserted (el, binding, vnode) {
        //     console.error('insertedvalidation', Vue.$validation);
        //     if (typeof binding.value === 'undefined') return;
        //     el[ctx] = {
        //         el,
        //         vm: vnode.context,
        //         expression: binding.value,
        //         validate_id: binding.value.key || vnode.context._uid
        //     };
        //     const args = arguments;
        //     el[ctx].vm.$nextTick(() => {
        //         setTimeout(() => { validate.call(el[ctx], args); }, 0);
        //     });
        // },
        componentUpdated (el, binding, vnode) {
            // console.warn('validation', Vue.$validation);
            if (typeof binding.value === 'undefined') return;
            el[ctx] = {
                el,
                vm: vnode.context,
                expression: binding.value,
                validate_id: binding.value.key || vnode.context._uid
            };
            const args = arguments;
            el[ctx].vm.$nextTick(() => {
                setTimeout(() => { validate.call(el[ctx], args); }, 0);
            });
        },
        unbind (el, binding, vnode) {
            // console.warn('unbind,validation', Vue.$validation);
            if (typeof binding.value === 'undefined') return;
            let field = binding.value.key || vnode.context._uid;
            Vue.$validation[field] = {};
        }
    },
    validation = {
    };

// === install ===

const install = function (Vue) {
    Vue.directive('validator', Validator);
    Vue.$validation = Vue.prototype.$validation = validation; // 
};

if (!Vue.prototype.$isServer && window.Vue) {
    window.Validator = Validator;
    Vue.use(install); // eslint-disable-line
}

Validator.install = install;
export default Validator;

