import vCell from '../vendor/v-cell.vue';
import * as utils from '../js/utils/utils';
import {Toast} from '../vendor/index';
import vDatetimePicker from '../vendor/v-datetime-picker';
import vButton from '../vendor/v-button';
import logger from '../js/utils/logger';

export default {
    components: { vCell, vDatetimePicker, vButton },
    data () {
        return {
            value1: null,
            value2: null,
            value3: null,
            value4: null,
            value5: new Date(),
            visible: false,
            visible2: false,
            visible3: false,
            visible4: false,
            visible5: false,
            value6: null,
            visible6: false
        };
    },
    computed: {
        formatedValue1 () {
            return this.value1 ? utils.formatTime(this.value1) : 'datetime picker';
        },
        formatedValue2 () {
            return this.value2 ? utils.formatTime(this.value2) : 'date picker';
        },
        formatedValue3 () {
            return this.value3 ? (this.value3) : 'time picker';
        },
        formatedValue4 () {
            return this.value4 ? utils.formatTime(this.value4) : '';
        },
        formatedValue5 () {
            return this.value5 ? utils.formatTime(this.value5) : '';
        },
        formatedValue6 () {
            return this.value6 ? utils.formatTime(this.value6) : 'smart picker';
        }
    },

    methods: {
        open (picker) {
            logger.log('datetime-picker.open: ');
            this.$refs[picker].open();
        },

        handleChange (value) {
            logger.log('datetime-picker.handleChange: ', value);
            Toast({
                message: ': ' + utils.formatTime(value),
                position: 'bottom'
            });
        },

        openPicker1 () {
            this.$refs.picker1.open();
        },
        handlePicker1 (value) {
            logger.log('datetime-picker.handlePicker1: ', value);
            this.value1 = value;
        },

        openPicker2 () {
